ion-searchbar {
  &.is-readonly {
    .searchbar-input {
      pointer-events: none;
    }
  }

  &.is-success-color-icon {
    ion-icon {
      --icon-color: var(--ion-color-success) !important;
    }
  }

}

.ion-float-clear:after {
  content: "";
  display: block;
  clear: both;
}




